<template>
	<v-container>
		<v-row align="center" justify="center" class="word-bar" v-if="!loading">
			<v-chip color="primary" style="font-size: large">{{
				selection ? selection.word : "..."
			}}</v-chip>
		</v-row>
		<v-container style="padding-top: 15px">
			<loading v-if="loading || loadingctrl" />
			<v-container v-else-if="selection.selected">
				<v-img
					class="my-3"
					contain
					:src="selection.selected.original"
					max-height="400px"
				/>
				{{ selection.selected.title }}
				<br />
				<div style="color: green">
					From seller: {{ selection.selected.user }}
				</div>
			</v-container>

			<v-container v-else-if="thumbnailView">
				<!-- <h2>User Select View</h2>
		{{ selection }}
    -->
				<v-row align-content="center" justify="center">
					<v-col
						v-for="(pict, index) in renderedThumbnails"
						:key="index"
						class="pict-card"
					>
						<tooltip :text="pict.title">
							<v-img
								class="my-3 thumbnail"
								contain
								:src="pict.thumbnail"
								max-height="200px"
								@click="
									() => showCarousel((page - 1) * 20 + index)
								"
								transition="fade"
							>
								<template v-slot:placeholder>
									<v-row
										class="fill-height ma-0"
										align="center"
										justify="center"
									>
										<v-progress-circular
											indeterminate
											color="primary lighten-5"
										></v-progress-circular>
									</v-row>
								</template>
							</v-img>
							<!-- <v-checkbox
							color="primary"
							class="img-chckbx"
							:value="selected === index"
							@click="() => toggleSelected(index)"
						/> -->
						</tooltip>
						<!-- <div style="font-size:x-small">{{ pict.title }}</div> -->
					</v-col>
					<!-- <v-col
						class="pict-card"
					>
					<v-lazy v-model="lazy"></v-lazy>
					</v-col> -->
				</v-row>
				<v-row justify="center" class="pagination-bar">
					<v-col cols="8">
						<v-container class="max-width">
							<v-pagination
								v-model="page"
								class="my-4"
								:length="totalPages"
							></v-pagination>
						</v-container>
					</v-col>
				</v-row>
			</v-container>
			<div class="carousel-container" v-else>
				<v-carousel
					v-model="carousel"
					hide-delimiters
					show-arrows-on-hover
					hide-delimiter-background
				>
					<div
						v-for="(pict, index) in pictures"
						:key="index"
						:text="pict.title"
					>
						<v-carousel-item>
							<v-sheet height="100%" class="transparent-sheet">
								<v-row
									class="fill-height"
									align="center"
									justify="center"
								>
									<v-img
										contain
										:src="pict.original"
										height="100%"
									>
										<template v-slot:placeholder>
											<v-img
												contain
												:src="pict.thumbnail"
												height="100%"
											>
											</v-img>
										</template>
									</v-img>
								</v-row>
							</v-sheet>
						</v-carousel-item>
					</div>
				</v-carousel>
				<v-row class="actions-row">
					<tooltip text="Select current image">
						<v-btn
							fab
							class="action-btn"
							small
							color="primary"
							@click="() => select(carousel)"
							:disabled="selectDisabled"
						>
							<v-icon> mdi-check </v-icon>
						</v-btn>
					</tooltip>
					<tooltip text="Close preview">
						<v-btn
							fab
							class="action-btn"
							small
							color="primary"
							@click="thumbnailView = true"
						>
							<v-icon> mdi-close </v-icon>
						</v-btn>
					</tooltip>
				</v-row>
				<!-- <confirm-dialog
					:text="`Once you select the image there's no going back, the corresponding seller will recive the points and the quiz related to this word (${selection.word}) would be closed`"
					v-model="confirmDialog"
					@accept="() => select(carousel)"
				/> -->
			</div>
		</v-container>
	</v-container>
</template>

<script>
import Loading from "@/common/components/Loading.vue";
import { mapActions, mapState } from "vuex";
import Tooltip from "@/common/components/Tooltip.vue";
import { closeSelection } from "../services/firebaseServices";
// import ConfirmDialog from "@/common/components/ConfirmDialog.vue";
import { getImages } from "../services/serpapiService";

export default {
	components: { Loading, Tooltip },
	data() {
		return {
			selection: null,
			selected: -1,
			thumbnailView: true,
			carousel: 0,
			confirmDialog: false,
			id: -1,
			page: 1,
			pictures: [],
			loadingctrl: false,
		};
	},
	// mounted(){
	// 	this.page = 0;
	// },
	computed: {
		...mapState("seller", ["currentRace"]),
		...mapState("common", ["currentUser"]),
		loading() {
			return !Boolean(this.currentRace);
		},
		renderedThumbnails() {
			return this.pictures.slice(
				20 * (this.page - 1),
				Math.min(20 * this.page, this.pictures.length)
			);
			// .map((p) => {
			// 	if (this.selection.pictLinks[p.original]) {
			// 		p.disabled = true;
			// 	}
			// 	return p;
			// });
		},
		totalPages() {
			return this.pictures.length / 20;
		},
		// carouselPictures() {
		// 	return this.pictures.filter((p) => {
		// 		return !this.selection.pictLinks[p.original];
		// 	});
		// },
		selectDisabled() {
			return Boolean(
				this.selection &&
					this.selection.pictLinks &&
					this.pictures &&
					this.selection.pictLinks[
						this.pictures[this.carousel].original
					]
			);
		},
	},
	watch: {
		loading: {
			immediate: true,
			handler(val) {
				if (val) return;
				let id = +this.$router.currentRoute.params.idx - 1;
				console.log({ id });
				if (
					Number.isNaN(id) ||
					id >= this.currentRace.length ||
					id < 0
				) {
					this.$toast.error("No se encuentra la ruta solicitada");
					this.$router.push("/404");
				}
				this.id = id;
				this.selection = this.currentRace[id];
				if (!this.selection.selected) this.fetchImages();
			},
		},
		thumbnailView(val) {
			if (val) {
				this.page = Math.floor(this.carousel / 20 + 1);
			}
		},
	},
	methods: {
		async fetchImages() {
			//todo implement api pagination
			this.loadingctrl = true;
			try {
				this.pictures = (await getImages(this.selection.word)).map(
					(p) => ({
						...p,
						lazy: false,
					})
				);
				this.loadingctrl = false;
				let mine = this.selection.pictures[this.currentUser.id];
				if (mine) {
					this.carousel = this.pictures.findIndex(
						(p) => p.original === mine.original
					);
					if (this.carousel > 0) {
						this.thumbnailView = false;
					}
				}
			} catch (e) {
				this.$toast.error(e.message);
			} finally {
			}
		},
		toggleSelected(i) {
			if (this.selected === i) {
				this.selected = null;
			} else {
				this.selected = i;
			}
		},
		showCarousel(index) {
			this.thumbnailView = false;
			this.carousel = index;
		},
		async select(idx) {
			try {
				await closeSelection(this.id, this.pictures[idx]);
				this.$toast.success("Image successfully selected");
				this.$router.push("/seller");
				// this.$toast.success("+3pts to " + idx);
				// this.$router.push("/seller");
			} catch (e) {
				this.$toast.error(e.message);
			}
		},
	},
};
</script>

<style>
.pict-card {
	background-color: transparent;
	/* display: flex; */
}
.thumbnail {
	cursor: pointer;
}
.transparent-sheet {
	background-color: transparent !important;
}
.carousel-container {
	position: relative;
}
.actions-row {
	position: absolute;
	top: -17px;
	right: 10px;
}
.action-btn {
	margin: 5px;
	z-index: 2;
}
.word-bar {
	position: fixed;
	z-index: 2;
	width: 100%;
	margin: 0;
	left: 0px;
}
.pagination-bar {
	position: fixed;
	z-index: 2;
	width: 100%;
	margin: 0;
	left: 0px;
	bottom: 0px;
}
</style>
