import axios from "axios";

let host = 'http://localhost:3002/serpapi/search.json' //middleware to serpapi.com due to site's cors policy

export async function getImages(word,page = 0){
    let resp = await axios.get(`${host}?q=${word}&ijn=${page}`);
    // let resp = await axios.get(full_url);
    if(resp.data.error){
        throw new Error(resp.data.error);					
    }
    let empty = resp.data.search_information && 
                resp.data.search_information.image_results_state === 'Fully empty'

    if(empty)return [];
    return resp.data.images_results
}